@font-face {
  font-family: "az2"; /* Nombre que usarás para referenciar la fuente */
  src: url("../experience//font//a2z.ttf") format("truetype"); /* Ruta al archivo TTF y formato */
  /* Puedes agregar más formatos si la fuente también está disponible en otros formatos como WOFF o WOFF2 */
}
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

:root {
  --opacity: 0;
  --position: absolute;
  --anim-state: paused;
  --span-color: black;
  --img: invert();
  --size: 0;
  --index: -1;
  --start-btn-scale: 1;
}
#renderer{
  transition: all 1s;
}
.dark{
  transition: all 1s;
  background-color: rgba(0, 0, 0, 0.3);
}
.fade-in {
  animation: fadeIn 2s easy;
}

.deactive {
  display: none !important;
}
.no-visible {
  transition: 1s;
  opacity: 0;
}

.element {
  width: fit-content;
  height: auto;
  padding: 15px;
  background-color: transparent;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: scale(1);
  transition: all 0.7s;
}
.element.desactive {
  visibility: hidden;
}
.element-b {
  color: rgb(147, 239, 255);
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-radius: 0.3rem;
  font-size: 35px;
  transition: all 0.7s;
  visibility: hidden;

  background: rgba(255, 255, 255, 0.059);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 2px solid rgb(147, 239, 255);

  -webkit-box-shadow: 0px 0px 25px 1px rgba(99, 232, 255, 0.3);
  -moz-box-shadow: 0px 0px 25px 1px rgba(99, 232, 255, 0.3);
  box-shadow: 0px 0px 25px 1px rgba(99, 232, 255, 0.3);
}
#overlay {
  width: 100vw;
  height: 100vh;
  position: absolute;
}
.active {
  opacity: 1 !important;
  visibility: visible !important;
  transform: scale(1); /* Escala inicial */
  transition: transform 1.5s, opacity 1.5s; /* Agregar transición a transform y opacity */
}
.not-active {
  opacity: 0 !important;
  transform: scale(0); /* Escalar el elemento a 0 */
  transition: all 0.8s cubic-bezier(1, 0, 0.9, 0.4);
}
.element-b:hover {
  transition: all 0.7s;

  transform: scale(1.1);
}
.element:hover {
  color: rgb(255, 255, 255);
  overflow: hidden;
  transition: all 0.7s;
  transform: scale(1.1);
  --position: relative; /* Oculta y saca del flujo de diseño */
  --anim-state: running;
  --opacity: 1;
  --span-color: black;
  --size: 18px;
  --index: 2;
}
.element span {
  font-family: az2;
  text-align: center;
  color: white;
}
.element img {
  padding: 10px;
  margin-bottom: 8px;
  width: 120px;
  height: auto;
  z-index: 3;
}
.element h2 {
  font-family: az2;

  text-align: center;
  color: white;
  padding: 0;
  margin: 0;
  margin: 0;
  transition: all 0.7s;
  pointer-events: none; /* Evita que el contenido sea interactivo durante la animación */
}
@keyframes round {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*Animacion de inicio
/* Definición de la animación */
/* Definición de la animación */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/*Drag Drop effects*/
.dg.ac {
  z-index: 100 !important ;
  display: none;
}

#cortain {
  background-color: transparent;
  position: fixed;
  width: 100vw;
  height: 100vh;
}
.cortain {
  background-color: black;
  position: fixed;
  z-index: 100;
  height: 0vh;
  width: 100%;

  transition: all 2s;
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.1) 5%,
    rgb(0, 0, 0) 100%
  );
}
.cortain__top {
  top: 0;
  transform: rotate(180deg);
}
.cortain__bottom {
  bottom: 0;
}
.active-cortain {
  height: 20vh;
}
/*ROOT*/
.root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 99;
  position: fixed;
  display: grid;
  place-content: center;
  align-items: center;
}

/*BANNERS*/
.navbar {
    
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: grid;
  place-content: center;
}
.navbar__logo {
    height: 60px;
    width:  60px;
    margin: 25px;
    background-image: url(../experience/img/logo.png);
    background-repeat: no-repeat;
    background-size: cover;
}
.footer {
  position: fixed;
  height: 120px;
  bottom: 0;
  left: 0;
  right: 0;
  color: #aaaaaa;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
}
.footer__action-btn {
  height: 40px;
  width:  120px;
  margin: 0 30px;
  border:  none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: contain;
}
.footer__text {
    font-family: az2;
    font-weight: 150;
}
.footer--hide-footer-btn {
    background-image: url("../experience/img/hide-footer-btn.png");
    background-position: left;

}
.footer--sound-btn {
    padding: 10px 0;
    background-image: url("../experience/img/sound-btn.png");
    background-position: right;

}

/*END BANNERS*/

/*LOAD SCREEN*/
.load-screen {
  width: 70vw;
  height: 70vh;
  display: grid;
  padding: 15vh 15vw;
  place-content: center;
  position: relative;
}
.action-container {
  width: 100%;
  height: 100%;
}
.action-container__start-btn {
  background: transparent;
  border: none;
  width: 260px;
  height: 260px;
  padding: 5px;
}
.start-btn__text {
  color: white;
  font-size: 1.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.start-btn__icon {
  width: 100%;
  height: 100%;
  background-image: url("../experience/img/begin-icon.png");
  background-repeat: no-repeat;
  background-size: cover;
}

/*END LOAD-SCREEN*/

/*FIRST SCREE*/
.first-screen {
  font-family: az2;
  font-weight: 300;
}
.first-screen__intro-container {
  color: white;
  width: fit-content;
  padding: 80px 3.5em;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.intro-container__title {
  font-weight: 600;
  font-size: 5.5em;
  line-height: 0.9;
  margin-bottom: 30px;
}
.intro-container__sub-title {
  margin-bottom: 20px;
  font-size: 1.2em;
}
.intro-container__control-panel {
  display: flex;
  width: 80%;
  justify-content: space-evenly;
  align-items: center;
  padding: 30px 15px;
}
.control-panel__intro-btn {
  width: fit-content;
  height: 55px;
  border: none;
  border-radius: 1em;
  color: white;
  font-size: 1em;
  background-color: transparent;
  position: relative;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 0 10px;
}
.control-panel__intro-btn--features-btn {
  background-image: url("../experience/img/intro-btn__features.png");
}
.control-panel__intro-btn--features-btn:hover {
  
}

.control-panel__intro-btn--launch-app-btn {
  background-image: url("../experience/img/intro-btn__launch-app.png");
}
.intro-btn__text {
  margin-right: 15px;
  margin-left: 25px;
}
.intro-btn__icon {
  width: 30px;
  height: 30px;
  background-image: url("../experience/img/intro-btn__icon.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5px;
}
.control-panel__btn {
}

/*END FIRST*/
